* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
}

.container {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
}


header {
    width: 100%;
    height: 60px;
    border-bottom: 1px solid #dddd;
}


.main-content {
    flex-grow: 1;
    height: 100%;
    background-color: cornsilk;

    padding: 20px;
}

.sidebar {
    width: 400px;
    border-left: 1px solid #dddd;
    padding: 20px;
}


.cart {
    display: flex;
    height: 60px;
    width: 100%;
    border-bottom: 1px solid #dddd;
    justify-content: flex-start;
    /* align-items: center; */
}

.cart-image {
    flex-basis: 90px;
    height: 100%;
    border-right: 1px solid #dddd;
    background-color:darkgray;
}

.cart-title {
    flex-grow: 1;
    padding-top: 5px;
    padding-left: 5px;
}



